@font-face {
  font-family: myFont;
  src: url(/snake/static/media/Gugi-Regular.deba6882.ttf);
}
body {
  background: url(/snake/static/media/funky-lines.7c2eac87.png);
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: bold;
}
h3,
.score,
.block .text {
  font-family: myFont;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  width: 80%;
  max-width: 600px;
  border-radius: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 0px;
  overflow: hidden;
  box-shadow: 5px 5px 5px lightgrey;
}

:root {
  --cell-border-color: rgb(37, 37, 37);
}
.grid-cell {
  display: inline-block;
  margin: 0;
  padding: 0;
  height: 15px;
  width: 15px;
  border-left: 0.5px solid rgb(37, 37, 37);
  border-left: 0.5px solid var(--cell-border-color);
  border-bottom: 0.5px solid rgb(37, 37, 37);
  border-bottom: 0.5px solid var(--cell-border-color);
}
/* border for every cell in the first row */
.row-0.grid-cell {
  border-top: 0.5px solid rgb(37, 37, 37);
  border-top: 0.5px solid var(--cell-border-color);
}
/* border on the right cell in every row */
.row.grid-cell:last-child {
  border-right: 0.5px solid rgb(37, 37, 37);
  border-right: 0.5px solid var(--cell-border-color);
}
.food {
  background: #6cca51;
}
.snake {
  background: #fbcb38;
}
.opponent-snake{
  background: tomato;
}
.content {
  text-align: center;
  background: url(/snake/static/media/dust_scratches.608ab5fe.png); }
  .content header {
    background: #858dbb;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between; }
    .content header h3 {
      padding: 5px;
      margin: 10px;
      font-size: 140%; }
    .content header h3 .status {
      display: inline-block;
      height: 10px;
      width: 10px;
      margin: 0 5px;
      border-radius: 50%;
      cursor: pointer;
      position: relative;
      right: 5px;
      -webkit-animation: 2s mix infinite;
              animation: 2s mix infinite; }
    .content header .score {
      padding: 7px;
      border: 1px solid transparent; }
    .content header .score.finished {
      border: 1px solid white;
      padding: 7px;
      background: none; }
  .content section {
    padding: 40px 50px;
    display: flex;
    justify-content: space-between; }
    .content section .board {
      display: inline-block; }
    .content section .sidebar {
      background: white;
      padding: 0 6px;
      border-radius: 5px;
      overflow: hidden;
      width: 150px; }
      .content section .sidebar .block {
        margin: 25px 0; }
        .content section .sidebar .block .hint {
          font-size: 8px;
          font-style: italic;
          color: #314a79;
          margin-bottom: 5px; }
        .content section .sidebar .block .text {
          font-weight: bold;
          color: #292727;
          opacity: .5; }

.grid-row {
  padding: 0;
  margin: 0;
  height: 15px; }

.in-progress {
  background: #fbcb38; }

.finished {
  background: #d84339; }

.not-started {
  background: #fff; }

.paused {
  background: #f1cbb5; }

.icon {
  position: absolute;
  right: 0;
  font-size: 25px;
  margin: 13px;
  color: #181818;
  cursor: pointer;
  -webkit-transition: 0.7s;
  transition: 0.7s; }

.icon:hover {
  -webkit-transform: scale(1.5) rotate(360deg);
          transform: scale(1.5) rotate(360deg); }

.game-mode {
  bottom: 32px; }

.play {
  bottom: 0; }

@-webkit-keyframes mix {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5); }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2); }
  100% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5); } }

@keyframes mix {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5); }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2); }
  100% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5); } }

